@import "vars";
@import "mixins";

.features {
  width: 100%;
  height: 1116px;
  background: $main-blue;
  margin-left: 0;
  margin-top: -160px;
  display: flex;
  overflow-y: hidden;
  position: relative;

  &__left {
    max-width: 362px;
  }

  &__title {
    @include section-title;
    color: #fff;

    &:before {
      background-image: url("../images/sub-title/sub-title-icon-light.svg");
    }
  }

  &__subtitle {
    @include subtitle-style;
  }

  &__image {
    flex-direction: row-reverse;
    width: 920px;
    height: 1116px;
    object-fit: cover;
    object-position: left top;
    clip-path: polygon(46% 0, 100% 0%, 54% 100%, 0% 100%);
    position: absolute;
    top: 0;
    right: 0;
  }

  &__point {
    font-size: 16px;
    line-height: 24px;
    color: #fff;
    margin-left: calc(64px + 32px);
    margin-bottom: 64px;
    width: 263px;
    position: relative;

    &:before {
      content: "";
      display: inline-block;
      width: 64px;
      height: 64px;
      position: absolute;
      left: calc(-64px - 32px);
      top: 16px;
      border-radius: 50%;
      background: #fff;
      background-image: url("../images/features/features-item-icon-1.svg");
    }
    &:nth-child(2):before {
      background-image: url("../images/features/features-item-icon-2.svg");
    }
    &:nth-child(3):before {
      background-image: url("../images/features/features-item-icon-3.svg");
    }
  }

  @media (max-width: 1159px) {
    & {
      height: max-content;
      padding-top: 160px;

      &__image {
        //display: none;
      }
    }
  }
  @media (max-width: 999px) {
    & {
      &__subtitle {
        margin-bottom: 124px;
      }

      &__image {
        display: none;
      }

      &__left {
        max-width: 100%;
      }

      &__elements {
        margin: auto;
        width: max-content;
      }

      &__point {
        margin-left: 0;
        margin-bottom: 124px;

        &:before {
          //left: 0;
          left: calc(64px + 32px);
          top: -84px;
        }
      }
    }
  }
}
